.projects{display:-ms-flexbox;display:flex;position:fixed;top:0;left:0;background:white;width:100%;height:100vh;padding:20px;z-index:100;overflow-y:auto}.projects .projects__inner{display:-ms-flexbox;display:flex;-ms-flex-flow:row;flex-flow:row;-ms-flex-wrap:wrap;flex-wrap:wrap;margin-bottom:auto}.projects .projects__add,.projects .project{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;-ms-flex-positive:1;flex-grow:1;width:18vw;max-width:calc(20vw - 16px);height:calc(18vw - 5px);-webkit-transition:background 0.3s;-o-transition:background 0.3s;transition:background 0.3s}.projects .projects__add:hover,.projects .project:hover{border-color:transparent;background:#EBEBEB}.projects .projects__add:not(:nth-child(5n)),.projects .project:not(:nth-child(5n)){margin-right:10px}.projects .projects__add:not(:nth-last-child(-n+4)),.projects .project:not(:nth-last-child(-n+4)){margin-bottom:10px}.projects .projects__add{font-size:64px}



